"use client";

import { useTheme } from "next-themes";
import { Sun, Moon, Laptop } from "lucide-react";
import { useEffect, useState } from "react";

export default function ThemeSwitcher() {
  const { setTheme, theme } = useTheme();
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setMounted(true);
  }, []);

  if (!mounted) return <p className="text-red-500">Loading...</p>;

  return (
    <div className="flex gap-4 justify-center items-center mt-4">
      <button
        onClick={() => setTheme("light")}
        className={`flex items-center gap-1 px-3 py-2 rounded border ${
          theme === "light" ? "bg-gray-200" : ""
        }`}
      >
        <Sun className="w-5 h-5" /> <span>Light</span>
      </button>

      <button
        onClick={() => setTheme("dark")}
        className={`flex items-center gap-1 px-3 py-2 rounded border ${
          theme === "dark" ? "bg-gray-800 text-white" : ""
        }`}
      >
        <Moon className="w-5 h-5" /> <span>Dark</span>
      </button>

      <button
        onClick={() => setTheme("system")}
        className={`flex items-center gap-1 px-3 py-2 rounded border ${
          theme === "system" ? "bg-blue-500 text-white" : ""
        }`}
      >
        <Laptop className="w-5 h-5" /> <span>System</span>
      </button>
    </div>
  );
}
